import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnimationController } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { SalesA45aComponent } from 'src/app/cafe/components/print/sales/A45/sales-a45a/sales-a45a.component';
import { SalesK57aComponent } from 'src/app/cafe/components/print/sales/K57/sales-k57a/sales-k57a.component';
import { SalesK80aComponent } from 'src/app/cafe/components/print/sales/K80/sales-k80a/sales-k80a.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhOrderInvoices, VhOrderInvoice, VhOrderInvoiceDetail } from 'src/app/cafe/interface/vh-order-invoice';

@Component({
  selector: 'app-detail-invoice',
  templateUrl: './detail-invoice.component.html',
  styleUrls: ['./detail-invoice.component.scss']
})
export class DetailInvoiceComponent {
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhOrderInvoices
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private animationCtrl: AnimationController,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) {
    let state = this.router.getCurrentNavigation().extras.state
    this.stateObj = state.stateObj
    this.bills = state.bills;
    this.getDataInvoice(state._id)
    this.data = state.data;
    this.init();
  }
  data: any;
  totalQuantity: number = 0;
  stateObj: any;
  bills: any;
  init(invoice: VhOrderInvoice = null, invoiceDetail: VhOrderInvoiceDetail[] = null) {
    this.invoice = new VhOrderInvoices(invoice, invoiceDetail,this.vhQueryCafe)
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee()),
        partner: this.vhQueryCafe.getlocalCustomer(this.invoice.getIdPartner()),
        paymentName: this.setPaymentName(this.invoice.getPaymentType())
      }

      this.invoice.getInvoiceDetail().forEach(item => {
        this.mapOfExpandedData[item._id] = this.convertTreeToList(item);
      });

    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhOrderInvoiceDetail[] = invoiceDetail.map(item => {
          if (item.toppings) {
            return {
              ...item,
              name: this.getNameFood(item.id_product, item.id_subproduct),
              toppings: item.toppings.map(element => {
                return { ...element, name: this.getNameFood(element.id_product, element.id_subproduct) }
              })
            }
          } if (item.combos) {
            let combo: any = {
              ...item,
              name: this.getNameFood(item.id_product, item.id_subproduct),
              combos: item.combos.map(element => {
                return { ...element, name: this.getNameFood(element.id_product, element.id_subproduct) }
              })
            }

            return { ...combo, subComboName: this.getSubCombo(combo.combos) }
          } else
            return {
              ...item,
              name: this.getNameFood(item.id_product, item.id_subproduct)
            }
        })
        this.init(invoice, invoiceDetailFresh)
        this.vhComponent.hideLoading(0)
      })
    })

  }

  getNameFood(id_prod: string, id_subProd: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = food.subs.find(item => item._id == id_subProd)
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`
  }
  /**Render các thành phần combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }

  type: 'show' | 'hide' = 'hide'
  viewInfo(type: 'show' | 'hide' = 'show') {
    this.type = type
    if (type == 'show')
      this.animationCtrl.create('view-info-popup-detail-show').addElement(document.querySelector('.view-info-popup-detail')).duration(300)
        .easing('cubic-bezier(0.16, 1, 0.3, 1)')
        .fromTo('transform', 'translateY(100%)', 'translateY(0)')
        .fromTo('visibility', 'hidden', 'visible').play()

    if (type == 'hide')
      this.animationCtrl.create('view-info-popup-detail-hide').addElement(document.querySelector('.view-info-popup-detail')).duration(300)
        .easing('cubic-bezier(0.16, 1, 0.3, 1)')
        .fromTo('transform', 'translateY(0)', 'translateY(100%)')
        .fromTo('visibility', 'visible', 'hidden').play()
  }

  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.getIdWallet()).name
    }
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/customer-debt/detail-customer-debt'], {
      state: { data: this.data, stateObj: this.stateObj, bills: this.bills }
    });
  }

  ngAfterViewChecked() {
    if (document.getElementById('sales-invoice-detail-today') && document.querySelector(".sales-cart-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById('sales-invoice-detail-today').clientHeight - document.querySelector(".sales-cart-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }

  /*****************************************************ANT TABLE********************************************************** */
  mapOfExpandedData: { [key: string]: any[] } = {};
  statusExpandAll: boolean = false
  /**Mở rộng/thu nhỏ 1 phần tử */
  collapse(array: any[], data: any, $event: boolean): void {
    if (!$event) {
      if (data.toppings) {
        data.toppings.forEach(d => {
          const target = array.find(a => a.id_product === d.id_product)!;
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }
  /** Chuyển đổi dữ liệu sang dạng cây */
  convertTreeToList(root: any): any[] {
    const stack: any[] = [];
    const array: any[] = [];
    const hashMap = {};
    stack.push({ ...root, level: 0, expand: false });

    while (stack.length !== 0) {
      const node = stack.pop()!;
      this.visitNode(node, hashMap, array);
      if (node.toppings) {
        for (let i = node.toppings.length - 1; i >= 0; i--) {
          stack.push({ ...node.toppings[i], level: node.level! + 1, expand: false, parent: node });
        }
      }
    }

    return array;
  }

  visitNode(node: any, hashMap: { [id_product: string]: boolean }, array: any[]): void {
    if (!hashMap[node.id_product]) {
      hashMap[node.id_product] = true;
      array.push(node);
    }
  }
  /**Mở rộng / thu nhỏ tất cả */
  expandAll() {
    this.statusExpandAll = !this.statusExpandAll
    this.invoice.getInvoiceDetail().forEach((detail, index) => {
      this.mapOfExpandedData[detail._id].forEach(item => item.expand = this.statusExpandAll)
    })
  }

}
