import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailCustomerDebtComponent } from './detail-customer-debt.component';
import { RouterModule, Routes } from '@angular/router';
import { DetailInvoiceComponent } from './detail-invoice/detail-invoice.component';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { DetailReceiptExpensesComponent } from './detail-receipt-expenses/detail-receipt-expenses.component';
import { CreateReceiptsComponent } from './create-receipts/create-receipts.component';
const routes: Routes = [
  {
    path: '',
    component:  DetailCustomerDebtComponent
  },
  {
    path: 'detail-invoice',
    component: DetailInvoiceComponent
  },
  {
    path: 'detail-receipt-expenses',
    component: DetailReceiptExpensesComponent
  },
  {
    path: 'create-receipt',
    component: CreateReceiptsComponent
  }
]

@NgModule({
  declarations: [
    DetailCustomerDebtComponent,
    DetailInvoiceComponent,
    DetailReceiptExpensesComponent,CreateReceiptsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule
  ]
})
export class DetailCustomerDebtModule { }
